// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent, act, waitFor } from '@testing-library/react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector';

// üî• Mocks optimis√©s pour le bon fonctionnement des tests d'animations
const mockStart = jest.fn((callback) => {
  // Ex√©cution synchrone pour les tests
  if (callback) {
    setImmediate(() => {
      act(() => {
        callback();
      });
    });
  }
});

const mockTiming = jest.fn(() => ({ start: mockStart }));
const mockParallel = jest.fn(() => ({ start: mockStart }));

// Le mock qui corrige l'erreur
const MockAnimatedValue = jest.fn().mockImplementation((initialValue) => {
  const mockValue = {
    _value: initialValue,
    // La m√©thode interpolate doit retourner un objet avec _nativeTag
    interpolate: jest.fn(() => ({ _nativeTag: 1 })),
    setValue: jest.fn(),
    addListener: jest.fn(),
    removeListener: jest.fn(),
    removeAllListeners: jest.fn(),
    stopAnimation: jest.fn(),
    resetAnimation: jest.fn(),
  };
  return mockValue;
});

const mockMultiply = jest.fn((anim1, anim2) => ({
  _nativeTag: 1, // Doit retourner un objet pour √™tre utilis√© dans style
  interpolate: jest.fn(() => ({ _nativeTag: 1 })),
}));


jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: MockAnimatedValue,
      timing: mockTiming,
      parallel: mockParallel,
      multiply: mockMultiply,
      View: RN.Animated.View, // Utiliser RN.Animated.View pour la compatibilit√©
    },
    ScrollView: RN.ScrollView,
    TouchableOpacity: RN.TouchableOpacity,
    Text: RN.Text,
    View: RN.View,
  };
});

jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return <View testID="linear-gradient" {...props}>{children}</View>;
  },
}));

jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () => StyleSheet.create({
    container: {},
    backgroundGradient: {},
    scrollView: {},
    scrollContent: {},
    categoryItemWrapper: {},
    categoryTouchable: {},
    selectedCategoryItem: {},
    selectedInner: {},
    selectedCategoryText: { color: '#FFFFFF' },
    sparkleContainer: {},
    sparkle: {},
    categoryItem: {},
    categoryInner: {},
    categoryText: {},
  });
});

describe('CategorySelector', () => {
  const categories = [
    { id: '1', name: 'Grammar' },
    { id: '2', name: 'Vocabulary' },
    { id: '3', name: 'Reading' },
  ];

  beforeEach(() => {
    jest.clearAllMocks();
    MockAnimatedValue.mockClear();
    mockTiming.mockClear();
    mockParallel.mockClear();
    mockStart.mockClear();
    mockMultiply.mockClear();
  });

  // ‚úÖ Tests de base
  describe('Basic rendering', () => {
    it('should render all categories correctly', () => {
      const { getByText } = render(
        <CategorySelector 
          categories={categories} 
          onSelectCategory={() => {}}
        />
      );
      expect(getByText('Grammar')).toBeTruthy();
      expect(getByText('Vocabulary')).toBeTruthy();
      expect(getByText('Reading')).toBeTruthy();
    });

    it('should display sparkle for selected category', () => {
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={() => {}}
        />
      );
      expect(getByText('‚ú®')).toBeTruthy();
    });
  });

  // üî• Tests d'interaction utilisateur
  describe('User interactions', () => {
    it('should call onSelectCategory when category is pressed', async () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          onSelectCategory={onSelectCategory}
        />
      );
      fireEvent.press(getByText('Vocabulary'));
      await waitFor(() => {
        expect(onSelectCategory).toHaveBeenCalledWith('2');
      });
    });

    it('should not call onSelectCategory for already selected category', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      fireEvent.press(getByText('Grammar'));
      expect(onSelectCategory).not.toHaveBeenCalled();
    });
  });

  // üéØ Tests d'animations
  describe('Animations', () => {
    it('should trigger parallel animations when selectedCategory changes', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      mockTiming.mockClear();
      mockParallel.mockClear();
      await act(async () => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="2"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      await waitFor(() => {
        expect(mockParallel).toHaveBeenCalledTimes(1);
        expect(mockTiming).toHaveBeenCalledTimes(2); // D√©s√©lection + S√©lection
      });
    });

    it('should prevent animation conflicts with isAnimatingRef', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      mockParallel.mockClear();
      // Premier changement (d√©clenche une animation)
      rerender(
        <CategorySelector
          categories={categories}
          selectedCategory="2"
          onSelectCategory={onSelectCategory}
        />
      );
      // Deuxi√®me changement tr√®s rapide (devrait √™tre bloqu√©)
      rerender(
        <CategorySelector
          categories={categories}
          selectedCategory="3"
          onSelectCategory={onSelectCategory}
        />
      );
      // Attendre la fin de la premi√®re animation
      await waitFor(() => {
        expect(mockParallel).toHaveBeenCalledTimes(1);
      });
      // Le deuxi√®me appel n'a pas d√©clench√© d'animation suppl√©mentaire
      expect(mockParallel).toHaveBeenCalledTimes(1);
    });
  });

  // üîÑ Tests de gestion dynamique des cat√©gories
  describe('Dynamic category management', () => {
    it('should handle category addition without crashing', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      await act(async () => {
        rerender(
          <CategorySelector
            categories={[...categories, { id: '4', name: 'Writing' }]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      expect(() => {
        rerender(
          <CategorySelector
            categories={[...categories, { id: '4', name: 'Writing' }]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      }).not.toThrow();
    });

    it('should handle category removal without crashing', async () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      await act(async () => {
        rerender(
          <CategorySelector
            categories={[categories[0]]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      expect(() => {
        rerender(
          <CategorySelector
            categories={[categories[0]]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      }).not.toThrow();
    });
  });

  // ‚úÖ Tests areEqual
  describe('areEqual memoization', () => {
    it('should return true for identical props', () => {
      const props = { selectedCategory: '1', primaryColor: '#000', categories: [{ id: '1', name: 'Cat1' }] };
      expect(areEqual(props, props)).toBe(true);
    });
  });
});